let myImage = document.querySelector('img');
let stance = "sitting"
let stancetext = document.getElementById('currentStance');


function changeimg() {
    let mySrc = myImage.getAttribute('src');
    if (stance === 'standing') {
        myImage.setAttribute('src', 'images/bow.png');
    } else if (stance === "sitting") {
        myImage.setAttribute('src', 'images/badge.png');
    }
}

function changestance() {
    if (stance === "standing") {
        standToSit()
    } else {
        sitToStand()
    }
}

function sitToStand() {
    stance = "standing"
    changeimg();
    stancetext.innerHTML = "I am " + stance
}

function standToSit() {
    stance = "sitting"
    changeimg();
    stancetext.innerHTML = "I am " + stance
}